import { createBrowserRouter } from "react-router-dom";
import { ListResult } from "../ListResult/ListResult";
import { DetailProduct } from "../DetailProduct/DetailProduct";
import { Unexpected } from "../Share/Unexpecetd";
import { Home } from "../Home/Home";

const router = createBrowserRouter([

    {
        path: "/mercado-libre-frontend/",
        element: <Home />,
        errorElement: <Unexpected />,
    },
    {
        path: "/mercado-libre-frontend/items",
        element: <ListResult />,
        errorElement: <Unexpected />,
    },
    {
        path: "/mercado-libre-frontend/items/:id",
        element: <DetailProduct />,
        errorElement: <Unexpected />,
    },
],{basename: "/mercado-libre-frontend"});
export { router };